html {
  height: 100%;
  box-sizing: border-box;
  background-color: #FFFFFF;
  font-family: "Opensans"; }

*, *::before, *::after {
  box-sizing: inherit; }

.page-body {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
  background-color: #FFFFFF;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  position: relative;
  overflow: auto; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 0px;
  height: 0px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.container {
  max-width: 1190px;
  min-width: 320px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .container {
    max-width: 100%; } }

@media (max-width: 767px) {
  .container {
    max-width: 90%; } }

.main {
  flex: 1 1 auto; }

.header {
  width: 100%;
  padding-top: 1.1rem; }

.header-burger {
  display: none; }

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px; }

.header-top__search label {
  position: relative;
  padding-left: 1.7rem; }
  .header-top__search label::before {
    content: "";
    position: absolute;
    width: 14px;
    height: 14px;
    background: url("../../img/search.svg");
    left: 0;
    top: 4px; }

.header-top__logo {
  position: relative;
  top: 0.8rem;
  right: 2.8rem; }

.header-top__input {
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: left;
  color: #1D1613;
  border: none; }
  .header-top__input::placeholder {
    color: #9B9997; }

.header-bottom {
  margin-top: 2.8rem; }

.header-bottom__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; }

.header-bottom__item {
  padding: 0 1rem; }
  .header-bottom__item:last-child {
    padding-right: 0;
    padding-left: 1.4rem; }

.header-bottom__link {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  color: #1D1613;
  text-decoration: none;
  transition: 0.2s; }

.header-bottom__link:hover {
  color: #A87B62; }

.header-bottom__link:active {
  opacity: 0.7; }

@media (max-width: 1023px) {
  .header {
    padding-top: 1.8rem; }
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 2.45rem; }
  .header-burger {
    display: block;
    background-color: #FFFFFF;
    border: none; }
  .header-top {
    display: block;
    padding: 0; }
  .header-top__search {
    display: none; }
  .header-top__logo {
    top: 0;
    right: 0.13em; }
  .header-bottom {
    display: none; } }

@media (max-width: 767px) {
  .header {
    padding-top: 0.8rem; }
  .header-container {
    padding: 0 0.5rem; }
  .header-top__logo img {
    transform: scale(0.8); } }

.footer {
  color: #FFFFFF;
  background-color: #000000;
  min-height: 338px;
  padding-top: 3rem; }

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  height: 225px; }

.singup {
  flex: 0 1 43%; }

.singup-title {
  font-family: "Cormorant";
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  letter-spacing: 0.03em;
  margin-top: 2.6rem;
  margin-bottom: 0;
  padding-left: 0.3rem;
  color: #FFFFFF; }

.singup-text {
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  margin-top: 0.6rem;
  padding-left: 0.6rem;
  color: #9B9997; }

.singup-field {
  display: flex;
  width: 100%;
  margin-top: 2.7rem;
  position: relative; }
  .singup-field:after {
    content: "";
    position: absolute;
    width: 98%;
    height: 1px;
    background-color: #FFFFFF;
    left: 10px;
    bottom: -8px; }

.singup-input {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #FFFFFF;
  border: none;
  background-color: #000000;
  margin-left: 0.6rem; }
  .singup-input::placeholder {
    color: #9B9997; }

.signup-button {
  margin-left: auto;
  margin-right: 2.7rem;
  cursor: pointer;
  position: relative;
  transition: 0.2s; }
  .signup-button::after {
    content: "";
    position: absolute;
    width: 29px;
    height: 9px;
    top: 5px;
    right: -41px;
    background: url("../../img/arrow-white.svg"); }

.signup-button:hover {
  color: #A87B62; }

.signup-button:active {
  opacity: 0.7; }

.functions {
  flex: 0 1 36.5%;
  margin-left: 5.5rem; }

.functions-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
