.socials-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 165px;
  margin-top: 3.1rem; }

.socials-list__item {
  flex: 0 1 25%; }

.socails-list__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  text-decoration: none;
  color: #FFFFFF;
