.socails-list__link--instagram:before {
  width: 15px;
  height: 15px;
  background: url("../../img/instagram.svg");
  left: -29px;
  top: 2px; }

.socails-list__link--pinterest:before {
  width: 15px;
  height: 15px;
  background: url("../../img/pin.svg");
  left: -29px;
  top: 3px; }

.socails-list__link--twitter:before {
  width: 15px;
  height: 13px;
  background: url("../../img/twitter.svg");
  left: -30px;
  top: 4px; }

@media (max-width: 1023px) {
